.page-content {
  height: 100%;
  width: 100%;
  grid-template-rows: 30% 40%;
  grid-template-columns: 100%;
  display: grid;
  position: fixed;
}

.text-city {
  text-align: center;
  line-height: 7vh;
}

.city {
  font-size: 8vh;
}

.country {
  font-size: 3vh;
}

.temp-container {
  grid-template-rows: 100%;
  grid-template-columns: 48% 52%;
  display: grid;
}

.temp-icon {
  width: 15vh;
  float: right;
  padding-top: 2vh;
}

.num-temp {
  text-align: left;
  display: flex;
}

.temperature {
  letter-spacing: -1px;
  font-size: 18vh;
  font-weight: 700;
}

.measure {
  width: 5vh;
  height: auto;
  margin: 0 auto auto 0;
}

@media (max-width: 600px) {
  .page-content {
    grid-template-rows: 38% 50%;
    grid-template-columns: 100%;
  }

  .city {
    font-size: 6vh;
  }

  .text-city {
    padding-top: 1.5vh;
  }

  .temp-icon {
    width: 15vh;
    padding-top: 1vh;
  }

  .temperature {
    font-size: 13vh;
  }

  .measure {
    width: 3.5vh;
  }
}

